<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference" aria-hidden="true">&#128279;</a></h1>
<h2 id="collapse-2"><a href="https://github.com/wxsms/uiv/blob/master/src/components/collapse/Collapse.vue">Collapse</a> <a class="header-anchor" href="#collapse-2" aria-hidden="true">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>v-model</code></td>
<td>Boolean</td>
<td>false</td>
<td>✔</td>
<td>Show / hide the component.</td>
</tr>
<tr>
<td><code v-pre>tag</code></td>
<td>String</td>
<td>div</td>
<td></td>
<td>The HTML tag that render the collapse component.</td>
</tr>
<tr>
<td><code v-pre>transition-duration</code></td>
<td>Number</td>
<td>350</td>
<td></td>
<td>Collapse transition speed. Use 0 to disable transition.</td>
</tr>
</tbody>
</table></div><h3 id="slots">Slots <a class="header-anchor" href="#slots" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>default</code></td>
<td>Replace as the collapse body.</td>
</tr>
</tbody>
</table></div><h3 id="events">Events <a class="header-anchor" href="#events" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Params</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>show</code></td>
<td></td>
<td>This event fires immediately when the v-model prop is set to true.</td>
</tr>
<tr>
<td><code v-pre>shown</code></td>
<td></td>
<td>This event is fired when a collapse element has been made visible to the user (will wait for CSS transitions to complete).</td>
</tr>
<tr>
<td><code v-pre>hide</code></td>
<td></td>
<td>This event is fired immediately when the v-model prop is set to false.</td>
</tr>
<tr>
<td><code v-pre>hidden</code></td>
<td></td>
<td>This event is fired when a collapse element has been hidden from the user (will wait for CSS transitions to complete).</td>
</tr>
</tbody>
</table></div></markdown-wrapper></template><script>
  

  
export default {components:{'collapse-example':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <div>\n      <btn type=\"primary\" @click=\"show=!show\">Click me!</btn>\n    </div>\n    <br/>\n    <collapse v-model=\"show\">\n      <div class=\"well\" style=\"margin-bottom: 0\">Hi there.</div>\n    </collapse>\n  </section>\n</div>",
    data () {
      return {
        show: false
      }
    }
  },'collapse-accordion':{"template":"<div class=\"markdown-live-example\">\n  <div class=\"panel-group\">\n    <div class=\"panel panel-default\">\n      <div class=\"panel-heading\" role=\"button\" @click=\"toggleAccordion(0)\">\n        <h4 class=\"panel-title\">Collapsible Group Item #1</h4>\n      </div>\n      <collapse v-model=\"showAccordion[0]\">\n        <div class=\"panel-body\">\n          Anim pariatur cliche reprehenderit, enim eiusmod high life accusamus terry richardson ad squid. 3 wolf\n          moon officia aute, non cupidatat skateboard dolor brunch. Food truck quinoa nesciunt laborum eiusmod.\n        </div>\n      </collapse>\n    </div>\n    <div class=\"panel panel-default\">\n      <div class=\"panel-heading\" role=\"button\" @click=\"toggleAccordion(1)\">\n        <h4 class=\"panel-title\">Collapsible Group Item #2</h4>\n      </div>\n      <collapse v-model=\"showAccordion[1]\">\n        <div class=\"panel-body\">\n          Anim pariatur cliche reprehenderit, enim eiusmod high life accusamus terry richardson ad squid. 3 wolf\n          moon officia aute, non cupidatat skateboard dolor brunch. Food truck quinoa nesciunt laborum eiusmod.\n        </div>\n      </collapse>\n    </div>\n    <div class=\"panel panel-info\">\n      <div class=\"panel-heading\" role=\"button\" @click=\"toggleAccordion(2)\">\n        <h4 class=\"panel-title\">Collapsible Group Item #3</h4>\n      </div>\n      <collapse v-model=\"showAccordion[2]\">\n        <div class=\"panel-body\">\n          Anim pariatur cliche reprehenderit, enim eiusmod high life accusamus terry richardson ad squid. 3 wolf\n          moon officia aute, non cupidatat skateboard dolor brunch. Food truck quinoa nesciunt laborum eiusmod.\n        </div>\n      </collapse>\n    </div>\n  </div>\n</div>",
    data () {
      return {
        showAccordion: [true, false, false]
      }
    },
    methods: {
      toggleAccordion (index) {
        if (this.showAccordion[index]) {
          this.$set(this.showAccordion, index, false)
        } else {
          this.showAccordion = this.showAccordion.map((v, i) => i === index)
        }
      }
    }
  }}}</script>


